/* styles.css */

body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  width: 110%;
  align-content: center;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  font-size: 2em;
}

h2 {
  font-size: 1.5em;
}

p {
  font-size: 1em;
}

.navbar {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 10px 0;
}

.navbar a {
  color: white;
  text-decoration: none;
  margin: 0 10px;
}

.navbar a:hover {
  text-decoration: underline;
}

.layout {
  background-color: rgb(72, 69, 90);
  width: 110%;
}

#footer {
  position: fixed;
  bottom: 20px; /* Adjusted for all screen sizes */
  right: 60%; /* Adjusted for all screen sizes */
  width: 200px; /* Default width for smaller screens */
  height: auto; /* Height adjusts according to content */
  transform: rotate(-90deg); /* Rotate text 90 degrees */
  background-color: rgba(72, 69, 90, 0);
  color: white;
  text-align: center; /* Center the text */
  line-height: 20px; /* Center text vertically */
  padding: 10px;
}

#footer h5 {
  font-size: 1em; /* Default font size for smaller screens */
  margin: 0; /* No margin around the text */
}

/* For small screens (up to 450px) */
@media screen and (max-width: 450px) {
#footer {
  width: 150px; /* Smaller width for small mobile screens */
  bottom: 60px; /* Adjust bottom spacing for small screens */
  right: 10px; /* Keep footer a bit away from the screen edge */
}

#footer h5 {
  font-size: 0.9em; /* Smaller text for small mobile screens */
}


}

/* For medium screens (451px to 768px) */
@media screen and (min-width: 451px) and (max-width: 768px) {
#footer {
  width: 200px; /* Slightly larger footer for medium screens */
  bottom: 70px; /* Adjust bottom spacing */
  right: 20px; /* Move the footer a bit to the right */
}

#footer h5 {
  font-size: 1em; /* Regular font size for medium screens */
}
}

/* For larger screens (above 768px) */
@media screen and (min-width: 769px) {
#footer {
  width: 30%; /* Wider footer for larger screens */
  bottom: 200px; /* Move the footer further up for larger screens */
  right: 0; /* Move it more to the right for bigger screens */
  
}

#footer h5 {
  font-size: 1.2em; /* Larger text for better visibility */
}
}
